
export default {
  methods: {
    handleDrag (e) {
      // 
      var fileList = e.dataTransfer.files
      var hasImg = this.fileUpload(fileList)
      if (hasImg) {
        this.isDrogover = false
        e.preventDefault()
      }
    },
    handleDragover (e) {
      this.isDrogover = true
      e.preventDefault()
    },
    handleDragleave (e) {
      this.isDrogover = false
      e.preventDefault()
    }
  }
}



